* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.cardContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  flex-wrap: wrap;
  background-color: #ffffff00;
}
.cardContainer .card {
  position: relative;
  width: 50vw;
  height: 500px;
  margin: 20px;
  box-shadow: 20px 20px 50px rgb(0, 0, 0, 0.5);
  background-color: #ffffff01;
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}
.cardContainer .card .contents {
  padding: 10px;
  text-align: center;
  transition: 0.5s;
  color: rgba(255, 255, 255, 0.5);
}
.cardContainer .card .contents .imgfluid {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}
.cardContainer .card .contents .title-1 {
  color: #fff;
  margin-top: 20px;
  font-weight: 600;
  font-family: "PT Serif", serif;
  font-size: 20px;
}
.cardContainer .card .contents .title-2 {
  color: rgb(255, 255, 255, 0.9);
  font-weight: 500;
  font-size: 18px;
}
.cardContainer .card .hoverContent {
  position: absolute;
  left: 10px;
  bottom: 0px;
  right: 10px;
  color: #fff;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  overflow: auto;
  max-height: 100%;
  font-weight: 400;
  top: 20px;
}
.cardContainer .card .hoverContent p span{
    font-weight: 900;
    font-size: 24px;
}
.cardContainer .card .hoverContent .imgfluid{
  width: 80%;
  height: auto;
  padding: 30px 20px;
}
.cardContainer .card:hover .hoverContent{
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    cursor: pointer;
}
.cardContainer .card:hover .contents{
 
  opacity: 0;
}
@media screen and (max-width: 600px) {
  .cardContainer .card {
    width: 80vw;
  }
  
}
.pageLableBox{
display: flex;
position: sticky;
justify-content: right;
align-items: top;
overflow-x: 90%;
margin-top: 125px;

} 
.pageLableBox .pageLable{
  position:fixed;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
