#container{
  z-index: 1;
}

.nav-sty {
  background-color: rgb(255, 255, 255, 0.8);
  border-radius: 20px;
  box-shadow: 20px 20px 50px #00000073;
  backdrop-filter: blur(1px);
}
.navbar-toggler {
  border: 0;
}
.navbar-toggler:focus,
.navbar-toggler:active,
navbar-toggler-icon:focus {
  outline: none;
  box-shadow: none;
  border: 0;
}
.toggler-icon {
  width: 30px;
  height: 3px;
  background-color: black;
  display: block;
  transition: all 0.3s;
}

.bar2 {
  margin: 5px 0px;
}

.navbar-toggler .bar1 {
  transform: rotate(45deg);
  transform-origin: 10% 10%;
}

.navbar-toggler .bar2 {
  opacity: 0;
  filter: alpha(opacity=0);
}

.navbar-toggler .bar3 {
  transform: rotate(-45deg);
  transform-origin: 10% 80%;
}
.navbar-toggler.collapsed .bar2 {
  opacity: 1;
  font: alpha(opacity=100);
}
.navbar-toggler.collapsed .bar3 {
  transform: rotate(0);
}
.navbar-toggler.collapsed .bar1 {
  transform: rotate(0);
}

 
.darky h1 {
  margin-top: 150px;
  text-align: center;
}

.np{
  background-color: #fff;
  color: #000;
  box-shadow: 10px 10px 10px #000;
  margin-left: 20px;
  margin-right: 20px;
  border: 1px solid rgb(197, 196, 196);
  font-weight: 600;
  font-size: 18px;
  border-radius: 0% 20%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.np:hover{
  background-color: rgba(0, 0, 0, 0.445);
  color: #fff;
  border: 1px solid rgb(0, 0, 0);
  border-radius:0% 20%;
}

.sticky{
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100px;
  z-index: 1;
}
.darkySticky{
  opacity: 0;
  overflow: hidden;
}
.formcontSticky{
  opacity: 0;
  overflow: hidden;
}

/* dropdown css */

.dropdown-containt{
  padding-left: 0px;
  display: none;
 
}
.dropdown-containt .drop-item{
  text-decoration: none;
  list-style: none;
  padding: 10px 15px;
  
  
}

.dropdown-containt .drop-item .drop-link{
 color:rgb(0, 0, 0);
 text-decoration: none;

}

.dropdown-containt .drop-item:hover{
  background-color: rgba(25, 51, 90, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);

}

.dropdown:hover .dropdown-containt{
  display:block;
  position: absolute;
  background-color: #ffffff00;
  min-width: 160px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0);
  z-index: 1;
  border-top: 1px solid rgba(0, 0, 0, 0.7);
  border-left: 1px solid rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);

}

/* Search bar css */
.search-container{
  background: #fff;
  margin-top: 10px;
  height: 30px;
  border-radius: 30px;
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.8s;
  /*box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
  inset -7px -7px 10px 0px rgba(0,0,0,.1),
 7px 7px 20px 0px rgba(0,0,0,.1),
 4px 4px 5px 0px rgba(0,0,0,.1);
 text-shadow:  0px 0px 6px rgba(255,255,255,.3),
            -4px -4px 6px rgba(116, 125, 136, .2);
text-shadow: 2px 2px 3px rgba(255,255,255,0.5);*/
box-shadow:  4px 4px 6px 0 rgba(255,255,255,.3),
            -4px -4px 6px 0 rgba(116, 125, 136, .2), 
  inset -4px -4px 6px 0 rgba(255,255,255,.2),
  inset 4px 4px 6px 0 rgba(0, 0, 0, .2);
}

.search-container:hover > .search-input{
  width: 80px;
}

.search-container .search-input{
  background: transparent;
  border: none;
  outline:none;
  width: 0px;
  font-weight: 500;
  font-size: 16px;
  transition: 0.8s;

}

.search-container .search-btn .fas{
  color: #5cbdbb;
}

@keyframes hoverShake {
0% {transform: skew(0deg,0deg);}
25% {transform: skew(5deg, 5deg);}
75% {transform: skew(-5deg, -5deg);}
100% {transform: skew(0deg,0deg);}
}

.search-container:hover{
animation: hoverShake 0.15s linear 3;
}

@media screen and(max-width:600px) {
    .search-container{
      max-width: 200px;
    }  
}